/* Pagination */
.pagination {
    margin: 10px 0 20px 20px;
    display: flex;
    align-items: center;
}
.pagination span {
    border: 1px solid gray;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 3px;
}
.pagination span:hover {
    background-color: #425242;
    color: #ffffff;
    transition: all .2s;
}
.pagination span:not(:last-child) {
    margin-right: 2px;
}

.selected_page {
    border: 2px solid black;
    color: #ffffff;
    background: #425242;
}

.pagination button {
    width: 35px;
    height: 25px;
    background: #ffffff;
    border: 1px solid #ce3039;
}
.prev {
    margin-right: 5px;
}
.next {margin-left: 3px;}
.prev, .next {
    position: relative;
}
.prev:before, .next:before {
    position: absolute;
    content: '←';
    display: inline;
    font-size: 25px;
    left: 17%;
    bottom: -2px;
}
.next:before {
    content: '→';
}
/* END Pagination */